<template>
    <nav-bar>
      <span slot="title" class="title">订单列表</span>
      <div slot="right-content" class="pull-xs-right">
        <input type="text" class="form-control" placeholder="输入订单号" />
      </div>
    </nav-bar>
    <table class="table">
      <thead>
        <tr>
          <th>订单编号 <i class="fa fa-sort"></i></th>
          <th>时间 <i class="fa fa-sort"></i></th>
          <th>总额 <i class="fa fa-sort"></i></th>
          <th>台号 <i class="fa fa-sort"></i></th>
          <th>员工 <i class="fa fa-sort"></i></th>
          <th>状态 <i class="fa fa-sort"></i></th>
          <th>操作</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="order in orders">
          <td>
            <a v-link="'/order/' + order.id">{{order.sn}}</a>
            <img src="../assets/new.gif" v-if="order.status == 'pedding'" />
          </td>
          <td>{{order.created_at}}</td>
          <td>￥{{order.total_price}}</td>
          <td>{{order.takeout ? '外卖' : order.room.name}}</td>
          <td>{{order.takeout ? '---' : order.worker.name}}</td>
          <td>{{order.status_text}}</td>
          <td>
            <button @click="remove(order)" class="btn btn-primary-outline btn-sm">删除</button>
          </td>
        </tr>
      </tbody>
    </table>
</template>
<script>
  import { showAlert, showLoading, hideLoading } from '../vuex/actions'

  export default {
    vuex: {
      getters: {
        isLogged: state => state.merchant.isLogged,
        access_token: state => state.merchant.merchant.access_token
      },
      actions: {
        showAlert,
        showLoading,
        hideLoading
      }
    },
    data () {
      return {
        orders: []
      }
    },
    route: {
      data (transition) {
        this.showLoading()
        this.$http.get('http://jiancan.me/api/m1/shops/orders.json', { access_token: this.$store.state.merchant.merchant.access_token }).then(function (response) {
          transition.next({
            orders: response.data
          })
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      }
    },
    methods: {
      remove (order) {
        this.showLoading()
        this.orders.$remove(order)
        this.$http.delete('http://jiancan.me/api/m1/orders/one.json', { access_token: this.$store.state.merchant.merchant.access_token, order_id: order.id }).then(function (response) {
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      }
    },
    components: {
      'nav-bar': require('../components/nav-bar.vue')
    }
  }
</script>



